<template>
	<div id="ali-pay">
		<div class="content" v-if="isWeixin">
			<div class="weixin-tip">
				<div class="weixin-tip-content">
					请在菜单中选择在浏览器中打开,<br>
					以完成支付
				</div>
			</div>
			<div class="weixin-tip-img iphone" v-if="isIos"></div>
			<div class="weixin-tip-img android" v-else></div>
		</div>
		<transition name="loading">
			<iloading v-show="showLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import loading from '@/components/loading'
	export default {
		data() {
			return {
				showLoading: !this.util.isWeixin(),
				isIos: this.util.isIos()
			}
		},
		components: {
			loading
		},
		created(){
			this.query = this.$route.query;
		},

		beforeDestory(){
			window.sessionStorage.replaceItem('flag');
		},

		mounted() {
			if(window.sessionStorage.getItem('flag') === null) {
				window.sessionStorage.setItem('flag', 1);
				window.location.href = window.location.href;
			}
			if(!this.util.isWeixin()) {
				this.util.request({
					url: 'system/paycenter/pay',
					method: 'POST',
					data: {
						pay_type: this.query.pay_type,
						order_type: this.query.order_type,
						id: this.query.order_id
					}
				}).then((result) => {
					this.showLoading = false;
					var result = result.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					result = result.message;
					if(!this.util.isWeixin()) {
						window.location.href = result.url;
					}
					return;
				})
			}
		},
	}
</script>

<style>
	#ali-pay .content{
		position: fixed;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
    	background: #F4F4F8;
		overflow-x: hidden;
        overflow-y: scroll;
	}
	#ali-pay .content::-webkit-scrollbar {
        display: none;
    }
    #ali-pay .content .weixin-tip{
		box-sizing: border-box;
	    position: absolute;
	    top: 15px;
	    right: 20px;
	    width: 265px;
	    padding: 55px 0 0;
	    text-align: left;
	    background: url(data:image/png;base64,) no-repeat right top;
	    background-size: 45px 68px;
    }
    #ali-pay .content .weixin-tip .weixin-tip-content{
		font-size: 15px;
		font-weight: 300;
		line-height: 20px;
    }
    #ali-pay .content .weixin-tip-img{
    	display: block;
    	padding: 110px 0 0;
    }
    #ali-pay .content .weixin-tip-img::after {
        display: block;
        margin: 15px auto;
        content: ' ';
        background-size: cover
    }

    #ali-pay .content .weixin-tip-img.iphone::after {
        width: 150px;
        height: 150px;
        background-image: url(data:image/png;base64,)
    }

    #ali-pay .content .weixin-tip-img.android::after {
        width: 173px;
        height: 240px;
        background-image: url(data:image/png;base64,)
    }
</style>


